:root {
    --font-titulo: 'Raleway', sans-serif;
    --font-parrafo: 'Open Sans', sans-serif;
    --color-principal: #4363D1;
    --color-secundario: #4bb82a;
    --color-terciario: #D14363;
    --color-boton: #3855b6;
  }

#bg{
    min-width: 1000px;
    height: 100vh;

}
#input-login{
    width: 30%;
}
p{
    font-family: 'Open Sans', sans-serif;
    font-family: var(--font-parrafo);
}
/* contenedor header */

.header-clases{
    background-image: linear-gradient(to right, #4363D1 0%, #3184f0 100%);
    background-image: linear-gradient(to right, var(--color-principal) 0%, #3184f0 100%);
    width: 100%;
    height: 300px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.ilustracion-header{
    background: linear-gradient(to right, #4363D1 0%, #3184f0 100%);
    background: linear-gradient(to right, var(--color-principal) 0%, #3184f0 100%);
  
}

/* contenedor textos header */

.textos-clases{
    width: 50%;
    color: #fff;
    z-index: 2;
}

.textos-clases h1{
    font-size: 50px;
    font-weight: 400;
    text-align: center;
    text-shadow: 0px 3px 5px rgba(165, 164, 164, 0.561) ;
}

.textos-clases h3{
    font-style: italic;
    font-size: 25px;
    font-weight: 400;
    text-align: center;
}

/* contenedor imagen header */

.header-clases img{
 width: 45%;
 z-index: 2;
}


/* subtitulo clases */

.subtitulo{
    padding: 40px;
    margin: 40px 0 40px 0;
    font-size: 50px;
    text-align: center;
}

section{
    background:linear-gradient(to right,#d42f31 0%, #fa6466 100%);
}


/* respomsive */

@media screen and (max-width:1180px){

    .textos-clases h1{
        font-size: 40px;
    }
}

@media screen and (max-width:900px){
    .textos-clases{
       width: 90%;
    }

    .subtitulo{
        font-size: 40px;
    }

    .ilustracion-header{
        margin-bottom: 100px;
    }

    .header-clases img{
        width: 50%;
    }
}

@media screen and (max-width:470px){
    .textos-clases{
       width: 95%;
    }
    .textos-clases h1{
        font-size: 30px;
     }
     

     .textos-clases h3{
        font-size: 18px;
     }
     
     .header-clases img{
         width: 70%;
     }
     .subtitulo{
        font-size: 35px;
    }
     
 
}
@media screen and (max-width:350px){

    .textos-clases h1{
        font-size: 25px;
     }   
 
}





/* contenedor del logo */
.logo-principal {
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 100%;
}

/* logo del colegio */

.logo-principal img {
  width: 25px;
  margin: 10px 0px 0px 12px;
}

/* navbar */

#navbar {
  justify-content: center;
  background-image: linear-gradient(
    to right,
    var(--color-principal) 0%,
    #3184f0 100%
  );
  display: flex;
  justify-content: space-between;
}

.activeClick {
  color: rgb(95, 91, 91);
  border-bottom: 1px solid rgba(12, 11, 11, 0.416);
}

#navbar li a {
  color: rgb(236, 236, 236);
  font-family: var(--font-parrafo);
}

/* navbar right */

.navbar-right {
  display: flex;
  align-items: center;
  padding-top: 15px;
  padding-bottom: 5px;
}
.navbar-right i {
  font-size: 25px;
  color: rgb(235, 233, 233);
  font-weight: 100;
  padding-right: 30px;
}
.navbar-right i:hover {
  color: #333;
  cursor: pointer;
}

.perfil-usuario.dropleft {
  width: 50px;
  height: 50px;
}
/* foto de perfil del usuario */

.perfil-usuario.dropleft .dropdown-toggle {
  width: 100%;
  border-radius: 100%;
}
/* estlos de los links del menu de perfil */

/* responsive */

@media screen and (max-width: 767px) {
  .navbar-right {
    justify-content: flex-end;
  }
  .perfil-usuario.dropleft {
    width: 60px;
    height: 60px;
  }
  /* contenedor del logo */
  .logo-principal {
    width: 60px;
    height: 60px;
  }

  /* logo del colegio */

  .logo-principal img {
    width: 35px;
    margin: 10px 0px 0px 12px;
  }
  #navbar li a {
    font-size: 18px;
  }
  #navbar {
    border-bottom: 1px solid rgba(255, 255, 255, 0.204);
  }
}

