/*   font-family: 'Open Sans', sans-serif;
font-family: 'Raleway', sans-serif;
 */

/* variables de las fuentes y colores que se usaran */
:root {
  --font-titulo: "Raleway", sans-serif;
  --font-parrafo: "Open Sans", sans-serif;
  --color-principal: #4363d1;
  --color-secundario: #63d143;
  --color-terciario: #d14363;
  --color-boton: #3855b6;
}

/* contenedor principal de todo el login */

.container-login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

/* contenedor izquierdo aqui esta el nombre del institulo y la ilustracion svg */

.textos-login {
  height: 100vh;
  background-image: linear-gradient(120deg, #ffffff 0%, #e7e6e6 100%);
  text-align: center;
  width: 50%;
  padding: 50px;
}

.textos-login h1 {
  font-size: 40px;
  font-family: "Raleway", sans-serif;
  font-family: var(--font-titulo);
  font-weight: 400;
}

.textos-login p {
  font-size: 20px;
  font-family: "Open Sans", sans-serif;
  font-family: var(--font-parrafo);
  margin-bottom: 30px;
  font-style: italic;
}

.textos-login img {
  width: 80%;
}

/* contenedor derecho aqui estado todo el formulario y el logo del institulo */

.formulario-login {
  padding: 50px;
  width: 50%;
  height: 100vh;
  background-image: linear-gradient(
    to top,
    #4363d1 0%,
    #3184f0 100%
  );
  background-image: linear-gradient(
    to top,
    var(--color-principal) 0%,
    #3184f0 100%
  );
  color: #fff;
}

/* titulo del formulario que dice ingresar */

.formulario-login h1 {
  text-align: center;
  font-size: 50px;
  font-weight: 300;
  margin-bottom: 15px;
}

/* contenedor del logo del instituto */

.logo-login {
  margin: auto;
  width: 100px;
  height: 100px;
  background-color: #fff;
  border-radius: 100%;
  padding: 20px 10px 20px 23px;
}
.logo-login img {
  width: 50px;
}

/* formualario de ingreso */

.formulario-login form {
  display: flex;
  margin: auto;
  width: 100%;
  color: #fff;
  flex-wrap: wrap;
  justify-content: center;
}

/* inputs del formulario */

.formulario-login form input {
  height: 50px;
  padding: 15px;
  margin-top: 20px;
  width: 70%;
  outline: none;
  font-family: "Open Sans", sans-serif;
  font-family: var(--font-parrafo);
  border-radius: 5px;
  border: none;
}
.formulario-login form input:focus {
  border: 2px solid #2ca9f1;
}

.formulario-login input[type="submit"] {
  margin-top: 0px;
  border-radius: 100px;
  outline: none;
  border: 1px solid #fff;
  background-color: #3855b6;
  background-color: var(--color-boton);
  color: #fff;
  margin-top: 20px;
}
.formulario-login input[type="submit"]:hover {
  background-color: transparent;
}
.formulario-login input[type="submit"]:focus {
  border: 1px solid #fff;
}

/* alerta por si el correo o el password esta mal */

.alert.alert-danger {
  margin-top: 20px;
  padding: 10px;
  margin-bottom: 0;
  line-height: 10px;
}

/* input de google */

#ingresarConGoogle {
  width: 70%;
  height: 50px;
  padding: 10px 0 20px 0;
  border: 1px solid #fff;
  border-radius: 5px;
  margin-top: 20px;
}

/* todo el responsive del login */

@media screen and (max-width: 1000px) {
  .textos-login h1 {
    font-size: 35px;
  }
  .textos-login {
    padding: 40px 15px 0 15px;
  }
  .formulario-login {
    padding: 40px 10px 0 10px;
  }

  .formulario-login form input {
    width: 80%;
  }
}
@media screen and (max-width: 900px) {
  .textos-login {
    width: 100%;
    height: 450px;
  }
  .formulario-login {
    width: 100%;
    height: 600px;
  }
  .textos-login img {
    width: 45%;
  }
}

@media screen and (max-width: 450px) {
  .textos-login h1 {
    font-size: 30px;
  }
  .formulario-login form input {
    width: 90%;
  }

  .textos-login img {
    width: 70%;
  }
}

